import React, { Component } from "react";

class NombreFoto extends Component {
    render(){
        return(
            <div style={{color: "#ffffff"}}>
                {this.props.nombre}
                <a href="https://myaccount.google.com/" target="_blank"><img src={this.props.photo} alt={"profile_photo"} width="50px" style={{borderRadius: "50%"}} className="mx-4"/></a>
            </div>
        )
    }
}

export default NombreFoto;
